import { useEffect, useState } from "react";
import { useNavigate, NavLink } from "react-router-dom";
import "./profile.css";
import { 
  ArrowRight, Sparkles, BookOpen, Trophy, Zap, Brain, Target, Rocket,
  GraduationCap, Users, Star, ChevronRight, Menu, X, LogIn, UserPlus,
  PlayCircle, CheckCircle, Lightbulb, BarChart3, Clock, Award
} from "lucide-react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";

const getStats = (roadmaps, quizStats) => {
  const stats = {};
  stats.progress = {};
  for (let topic in quizStats) {
    let numWeightage = 0;
    let completedWeightage = 0;
    if (roadmaps[topic]) {
      Object.keys(roadmaps[topic]).forEach((week, i) => {
        roadmaps[topic][week].subtopics.forEach((subtopic, j) => {
          numWeightage += parseInt(subtopic.time.replace(/^\D+/g, ""));
          if (
            quizStats[topic] &&
            quizStats[topic][i + 1] &&
            quizStats[topic][i + 1][j + 1]
          ) {
            completedWeightage += parseInt(subtopic.time.replace(/^\D+/g, ""));
          }
        });
      });
    }
    stats.progress[topic] = {
      total: numWeightage,
      completed: completedWeightage,
    };
  }
  return stats;
};

const ProfilePage = () => {
  ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);
  const navigate = useNavigate();
  const topics = JSON.parse(localStorage.getItem("topics")) || {};
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [showSignIn, setShowSignIn] = useState(false);
  const [showSignUp, setShowSignUp] = useState(false);
  const [authEmail, setAuthEmail] = useState("");
  const [authPassword, setAuthPassword] = useState("");
  const [authName, setAuthName] = useState("");
  const [authError, setAuthError] = useState("");
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [stats, setStats] = useState({});
  const [percentCompletedData, setPercentCompletedData] = useState({});
  
  const colors = ["#a855f7", "#06b6d4", "#10b981", "#f59e0b", "#ef4444", "#6366f1", "#ec4899"];

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("currentUser"));
    if (user && user.loggedIn) {
      setIsLoggedIn(true);
    }
    const roadmaps = JSON.parse(localStorage.getItem("roadmaps")) || {};
    const quizStats = JSON.parse(localStorage.getItem("quizStats")) || {};
    setStats(getStats(roadmaps, quizStats));
  }, []);

  useEffect(() => {
    let progress = stats.progress || {};
    let labels = Object.keys(progress);
    let data = Object.values(progress).map(
      (topicProgress) => (topicProgress.completed * 100) / topicProgress.total
    );
    let backgroundColors = Object.values(progress).map(
      (topicProgress, index) => colors[index % colors.length]
    );
    setPercentCompletedData({
      labels: labels,
      datasets: [{
        label: "% Completed",
        data: data,
        backgroundColor: backgroundColors,
        borderColor: backgroundColors,
        borderWidth: 1,
      }],
    });
  }, [stats]);

  const handleSignIn = (e) => {
    e.preventDefault();
    const users = JSON.parse(localStorage.getItem("users")) || {};
    if (users[authEmail] && users[authEmail].password === authPassword) {
      localStorage.setItem("currentUser", JSON.stringify({
        email: authEmail, name: users[authEmail].name, loggedIn: true
      }));
      setIsLoggedIn(true);
      setShowSignIn(false);
      setAuthError("");
    } else {
      setAuthError("Invalid email or password");
    }
  };

  const handleSignUp = (e) => {
    e.preventDefault();
    const users = JSON.parse(localStorage.getItem("users")) || {};
    if (users[authEmail]) {
      setAuthError("Email already exists");
      return;
    }
    users[authEmail] = { name: authName, password: authPassword };
    localStorage.setItem("users", JSON.stringify(users));
    localStorage.setItem("currentUser", JSON.stringify({
      email: authEmail, name: authName, loggedIn: true
    }));
    setIsLoggedIn(true);
    setShowSignUp(false);
    setAuthError("");
  };

  const handleLogout = () => {
    localStorage.removeItem("currentUser");
    setIsLoggedIn(false);
  };

  const features = [
    { icon: <Brain size={24} />, title: "AI-Powered Roadmaps", desc: "Get personalized learning paths tailored to your goals and skill level" },
    { icon: <Target size={24} />, title: "Smart Quizzes", desc: "Test your knowledge with AI-generated quizzes after each topic" },
    { icon: <BarChart3 size={24} />, title: "Progress Tracking", desc: "Monitor your learning journey with detailed analytics" },
    { icon: <Clock size={24} />, title: "Time Management", desc: "Learn at your own pace with flexible scheduling" },
    { icon: <Lightbulb size={24} />, title: "Adaptive Learning", desc: "Content adjusts based on your performance" },
    { icon: <Award size={24} />, title: "Achievements", desc: "Earn badges and track your accomplishments" },
  ];

  const howItWorks = [
    { step: "01", title: "Choose Your Topic", desc: "Select what you want to learn from any field" },
    { step: "02", title: "Set Your Timeline", desc: "Define how much time you can dedicate" },
    { step: "03", title: "Get AI Roadmap", desc: "Receive a personalized learning path" },
    { step: "04", title: "Learn & Quiz", desc: "Study the content and test your knowledge" },
  ];

  const testimonials = [
    { name: "Alex Chen", role: "Software Developer", text: "OrbitAI helped me master Python in just 4 weeks!", rating: 5 },
    { name: "Sarah Miller", role: "Data Scientist", text: "The personalized roadmaps are incredibly effective.", rating: 5 },
    { name: "James Wilson", role: "Student", text: "Best learning platform I've ever used. Highly recommend!", rating: 5 },
  ];

  return (
    <div className="orbit-page">
      {/* Navbar */}
      <nav className="navbar">
        <div className="nav-container">
          <div className="nav-logo">
            <span className="logo-icon"></span>
            <span className="logo-text">ORBIT</span>
          </div>
          
          <div className={`nav-links ${mobileMenuOpen ? 'active' : ''}`}>
            <a href="#home" onClick={() => setMobileMenuOpen(false)}>Home</a>
            <a href="#features" onClick={() => setMobileMenuOpen(false)}>Features</a>
            <a href="#how-it-works" onClick={() => setMobileMenuOpen(false)}>How It Works</a>
            {Object.keys(topics).length > 0 && (
              <a href="#courses" onClick={() => setMobileMenuOpen(false)}>My Courses</a>
            )}
            <a href="#testimonials" onClick={() => setMobileMenuOpen(false)}>Reviews</a>
          </div>

          <div className="nav-buttons">
            {isLoggedIn ? (
              <>
                <button className="nav-btn secondary" onClick={handleLogout}>Log Out</button>
                <button className="nav-btn primary" onClick={() => navigate("/topic")}>
                  <Zap size={16} /> Dashboard
                </button>
              </>
            ) : (
              <>
                <button className="nav-btn secondary" onClick={() => { setShowSignIn(true); setShowSignUp(false); }}>
                  Log In
                </button>
                <button className="nav-btn primary" onClick={() => { setShowSignUp(true); setShowSignIn(false); }}>
                  <Zap size={16} /> Try For Free
                </button>
              </>
            )}
          </div>

          <button className="mobile-menu-btn" onClick={() => setMobileMenuOpen(!mobileMenuOpen)}>
            {mobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
      </nav>

      {/* Auth Modal */}
      {(showSignIn || showSignUp) && (
        <div className="auth-modal-overlay" onClick={() => { setShowSignIn(false); setShowSignUp(false); }}>
          <div className="auth-modal" onClick={(e) => e.stopPropagation()}>
            <button className="auth-close" onClick={() => { setShowSignIn(false); setShowSignUp(false); }}>
              <X size={24} />
            </button>
            <div className="auth-modal-header">
              <div className="auth-modal-icon">
                {showSignIn ? <LogIn size={28} /> : <UserPlus size={28} />}
              </div>
              <h2>{showSignIn ? "Welcome Back" : "Create Account"}</h2>
              <p>{showSignIn ? "Sign in to continue learning" : "Start your learning journey"}</p>
            </div>
            <form onSubmit={showSignIn ? handleSignIn : handleSignUp} className="auth-modal-form">
              {authError && <div className="auth-modal-error">{authError}</div>}
              {showSignUp && (
                <input
                  type="text"
                  placeholder="Full Name"
                  value={authName}
                  onChange={(e) => setAuthName(e.target.value)}
                  required
                />
              )}
              <input
                type="email"
                placeholder="Email Address"
                value={authEmail}
                onChange={(e) => setAuthEmail(e.target.value)}
                required
              />
              <input
                type="password"
                placeholder="Password"
                value={authPassword}
                onChange={(e) => setAuthPassword(e.target.value)}
                required
              />
              <button type="submit" className="auth-modal-submit">
                {showSignIn ? "Sign In" : "Create Account"}
              </button>
            </form>
            <div className="auth-modal-switch">
              {showSignIn ? (
                <p>Don't have an account? <button onClick={() => { setShowSignIn(false); setShowSignUp(true); }}>Sign Up</button></p>
              ) : (
                <p>Already have an account? <button onClick={() => { setShowSignUp(false); setShowSignIn(true); }}>Sign In</button></p>
              )}
            </div>
          </div>
        </div>
      )}

      {/* Hero Section */}
      <section id="home" className="hero">
        <div className="hero-bg">
          <div className="orbit-line orbit-1"></div>
          <div className="orbit-line orbit-2"></div>
          <div className="orbit-line orbit-3"></div>
          <div className="planet planet-1"></div>
          <div className="planet planet-2"></div>
          <div className="planet planet-3"></div>
          <div className="planet planet-sun"></div>
        </div>
        
        <div className="hero-content">
          <div className="hero-badge">
            <Zap size={14} />
            <span>10K+ STUDENTS WORLDWIDE</span>
          </div>
          
          <h1 className="hero-title">
            Elevate Your Learning Journey<br />
            With Smarter <span className="gradient-text">AI Guidance</span>
          </h1>
          
          <p className="hero-subtitle">
            Take control of your education with responsible AI that provides 
            end-to-end quality guidance for your learning goals.
          </p>
          
          <div className="hero-buttons">
            <button className="hero-btn primary" onClick={() => navigate("/topic")}>
              <Zap size={18} /> Start Learning
            </button>
            <button className="hero-btn secondary" onClick={() => document.getElementById('how-it-works').scrollIntoView({ behavior: 'smooth' })}>
              <PlayCircle size={18} /> How it works
            </button>
          </div>

          <div className="hero-stats">
            <div className="stat">
              <span className="stat-num">10K+</span>
              <span className="stat-label">Active Learners</span>
            </div>
            <div className="stat">
              <span className="stat-num">500+</span>
              <span className="stat-label">Topics Available</span>
            </div>
            <div className="stat">
              <span className="stat-num">98%</span>
              <span className="stat-label">Success Rate</span>
            </div>
          </div>
        </div>
      </section>

      {/* Features Section */}
      <section id="features" className="features-section">
        <div className="section-header">
          <span className="section-badge">Features</span>
          <h2>Everything You Need to <span className="gradient-text">Succeed</span></h2>
          <p>Powerful tools designed to accelerate your learning journey</p>
        </div>
        <div className="features-grid">
          {features.map((feature, i) => (
            <div key={i} className="feature-card">
              <div className="feature-icon">{feature.icon}</div>
              <h3>{feature.title}</h3>
              <p>{feature.desc}</p>
            </div>
          ))}
        </div>
      </section>

      {/* How It Works */}
      <section id="how-it-works" className="how-section">
        <div className="section-header">
          <span className="section-badge">Process</span>
          <h2>How <span className="gradient-text">OrbitAI</span> Works</h2>
          <p>Get started in minutes with our simple process</p>
        </div>
        <div className="steps-grid">
          {howItWorks.map((item, i) => (
            <div key={i} className="step-card">
              <span className="step-number">{item.step}</span>
              <h3>{item.title}</h3>
              <p>{item.desc}</p>
              {i < howItWorks.length - 1 && <ChevronRight className="step-arrow" size={24} />}
            </div>
          ))}
        </div>
      </section>

      {/* My Courses Section */}
      {Object.keys(topics).length > 0 && (
        <section id="courses" className="courses-section">
          <div className="section-header">
            <span className="section-badge">Dashboard</span>
            <h2>Continue <span className="gradient-text">Learning</span></h2>
            <p>Pick up where you left off</p>
          </div>
          <div className="courses-grid">
            {Object.keys(topics).map((course, i) => (
              <NavLink
                key={i}
                className="course-card"
                to={"/roadmap?topic=" + encodeURI(course)}
                style={{ "--accent": colors[i % colors.length] }}
              >
                <div className="course-icon"><BookOpen size={24} /></div>
                <div className="course-info">
                  <h3>{course}</h3>
                  <span className="course-time">{topics[course].time}</span>
                  <span className="course-level">{topics[course].knowledge_level}</span>
                </div>
                <ArrowRight size={20} className="course-arrow" />
              </NavLink>
            ))}
          </div>
        </section>
      )}

      {/* Testimonials */}
      <section id="testimonials" className="testimonials-section">
        <div className="section-header">
          <span className="section-badge">Reviews</span>
          <h2>What Our <span className="gradient-text">Students</span> Say</h2>
          <p>Join thousands of satisfied learners</p>
        </div>
        <div className="testimonials-grid">
          {testimonials.map((t, i) => (
            <div key={i} className="testimonial-card">
              <div className="testimonial-stars">
                {[...Array(t.rating)].map((_, j) => <Star key={j} size={16} fill="#f59e0b" color="#f59e0b" />)}
              </div>
              <p>"{t.text}"</p>
              <div className="testimonial-author">
                <div className="author-avatar">{t.name.charAt(0)}</div>
                <div>
                  <h4>{t.name}</h4>
                  <span>{t.role}</span>
                </div>
              </div>
            </div>
          ))}
        </div>
      </section>

      {/* CTA Section */}
      <section className="cta-section">
        <div className="cta-content">
          <h2>Ready to Start Your <span className="gradient-text">Learning Journey</span>?</h2>
          <p>Join thousands of learners who are already transforming their skills with OrbitAI</p>
          <button className="cta-btn" onClick={() => navigate("/topic")}>
            <Rocket size={20} /> Get Started Free
          </button>
        </div>
      </section>

      {/* Footer */}
      <footer className="footer">
        <div className="footer-content">
          <div className="footer-brand">
            <div className="nav-logo">
              <span className="logo-icon"></span>
              <span className="logo-text">ORBIT</span>
            </div>
            <p>AI-powered personalized learning platform</p>
          </div>
          <div className="footer-links">
            <div className="footer-col">
              <h4>Product</h4>
              <a href="#features">Features</a>
              <a href="#how-it-works">How It Works</a>
              <a href="#testimonials">Reviews</a>
            </div>
            <div className="footer-col">
              <h4>Company</h4>
              <a href="#">About</a>
              <a href="#">Careers</a>
              <a href="#">Contact</a>
            </div>
            <div className="footer-col">
              <h4>Legal</h4>
              <a href="#">Privacy</a>
              <a href="#">Terms</a>
            </div>
          </div>
        </div>
        <div className="footer-bottom">
          <p> 2026 OrbitAI. All rights reserved.</p>
        </div>
      </footer>
    </div>
  );
};

export default ProfilePage;
