import { NavLink } from "react-router-dom";
import "./header.css";
import { CircleUser, Home, Orbit } from "lucide-react";

const Header = () => {
  return (
    <header>
      <div className="logo-container">
        <Orbit size={32} strokeWidth={1.5} className="logo-icon" />
        <span className="logo-text">Orbit<span className="logo-highlight">AI</span></span>
      </div>
      <NavLink to="/" className={"Home"}>
        <Home size={40} strokeWidth={1} color="white"></Home>
      </NavLink>
      <NavLink to="/profile" className={"ProfileAvatar"}>
        <CircleUser size={50} strokeWidth={1} color="white"></CircleUser>
      </NavLink>
    </header>
  );
};

export default Header;
